<template>
    <v-flex sm12 md10 offset-md1 class=" pt-xs-2 mt-xs-2 pt-sm-4 mt-sm-4 pt-md-8 mt-md-8 pt-4 mt-4" >
        <v-card
        elevation="4" light
        outlined>
            <v-progress-linear
            :active="loading"
            indeterminate
            color="primary"
            ></v-progress-linear>
            <v-card-title
            class="blue-grey darken-3"
            dark>
              <v-layout align-center justify-space-between>
                <h3 class="text-xs white--text">
                  {{ platformName }}
                </h3>
                <v-flex>
                  <v-img :alt="platformName" class="ml-3" contain height="48px" position="center right" 
                  :src="$baseUrl+'storage/icons/110x92px.png'"
                  :lazy-src="$baseUrl+'storage/icons/110x92px.png'"
                  ></v-img>
                </v-flex>
              </v-layout>
            </v-card-title>
            <v-divider></v-divider>

            <v-card-text class="px-4">
                <p>{{$t('sign_up')}}</p>
                <v-form ref="registerForm" v-model="valid" lazy-validation>
                    <v-row>
                        <v-col cols="12" sm="6" md="6">
                            <v-text-field prepend-icon="mdi-account" dense v-model="formReg.name"  :label="$t('f_name')" maxlength="255" :counter="255" required :rules="[rules.required]"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="6">
                            <v-text-field dense v-model="formReg.lastName"  :label="$t('l_name')" maxlength="255" :counter="255" :rules="[rules.required]" required></v-text-field>
                        </v-col>
                        <v-col cols="12">
                            <v-text-field autocomplete="off" prepend-icon="mdi-email" dense v-model="formReg.email" :rules="emailRules" label="E-mail" :counter="255" required></v-text-field>
                        </v-col>
                            <v-col v-if="yPhone" cols="12" sm="12" md="12" align="center" justify="center" center>
                                <div >{{$t('y_phone')}} <code>+{{yPhone}}</code></div>
                            </v-col>
                        <v-col cols="12" sm="6" md="6">
                            <v-autocomplete
                            prepend-icon="mdi-earth"
                            v-model="formReg.prefix_phone_1"
                            :items="countrys"
                            :label="$t('country')"
                            required
                            item-text="country"
                            item-value="phone"
                            :rules="[rules.required]"
                            dense
                            >
                            </v-autocomplete>
                        </v-col>
                        <v-col cols="12" sm="6" md="6">

                            <v-text-field prepend-icon="mdi-cellphone" :disabled="canPhone" dense v-model="formReg.phone1" type="number"  :rules="[rulesPhone.required,rulesPhone.min,rulesPhone.max]" :label="$t('phone_number')" minlength="4" maxlength="15" :counter="15" required></v-text-field>
                        </v-col>
                        <v-col cols="12">
                            <div>

                                    <v-menu
                                    ref="menu"
                                    v-model="menu"
                                    :close-on-content-click="false"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                    dense
                                    >
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-text-field
                                        v-model="date"
                                        :label="$t('b_date')"
                                        prepend-icon="mdi-calendar"
                                        readonly
                                        v-bind="attrs"
                                        v-on="on"
                                        dense
                                        :rules="[rules.required]"
                                        ></v-text-field>
                                    </template>
                                    <v-date-picker
                                        v-model="date"
                                        :active-picker.sync="activePicker"
                                        :max="new Date(new Date().setFullYear(new Date().getFullYear() - 18)).toISOString().substr(0, 10)"
                                        min="1871-01-01"
                                        @change="save"
                                        dense
                                    ></v-date-picker>
                                    </v-menu>
                                </div>
                        </v-col>
                        <v-col cols="12">
                            <v-text-field prepend-icon="mdi-lock" dense v-model="formReg.password" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'" :rules="[rules.required, rules.min,rules.password]" :type="show1 ? 'text' : 'password'" name="input-10-1" :label="$t('password')" hint="At least 8 characters" counter @click:append="show1 = !show1"></v-text-field>
                        </v-col>
                        <v-col cols="12">
                            <v-text-field prepend-icon="mdi-lock-alert" dense block v-model="formReg.password_confirmation" :rules="[ rules.required, rules.min,passwordMatch ]"  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"   :type="show1 ? 'text' : 'password'" name="input-10-1" :label="$t('password_confirm')" counter @click:append="show1 = !show1"></v-text-field>
                        </v-col>
                        <v-col cols="12">
                            <v-checkbox dense v-model="formReg.terms_conditions" :rules="[rules.required]">
                            <template v-slot:label>
                                <div>
                                {{$t('i_have_read_and_agree')}}
                                <v-tooltip bottom>
                                    <template v-slot:activator="{ on }">
                                    <a
                                        target="_blank"
                                        href="/terms"
                                        @click.stop
                                        v-on="on"
                                    >
                                        {{$t('terms_service')}}
                                    </a>
                                    </template>
                                    {{$t('open_terms_and_conditions')}}
                                </v-tooltip>
                                </div>
                            </template>
                            </v-checkbox>
                        </v-col>
                        <v-spacer></v-spacer>
                        <v-col class="d-flex" cols="12" sm="12" xsm="12" align-end>
                            <v-btn elevation="1" block :loading="loading" :disabled="!valid" color="black" class="white--text" @click="validate">{{$t('register')}}</v-btn>
                        </v-col>
                    </v-row>
                </v-form>
            </v-card-text>
            <v-divider></v-divider>

            <v-card-actions>
                <lang/>
            <v-spacer></v-spacer>
            <v-btn text
            to="/login"
            >
                Cancel
            </v-btn>
            </v-card-actions>
        </v-card>
          <v-layout align-center justify-space-between>
            <p class="caption my-3">
              <a href="https://app.eatinmore.com/privacy">Privacy Policy</a>
              |
              <a href="https://app.eatinmore.com/terms" target="_blank">Terms of Service</a>
            </p>
            <p class="caption my-3">Powered by <a href="https://eatinmore.com" target="_blank">EatInMore</a></p>
          </v-layout>
    </v-flex >
</template>

<script>

import lang from "../Helpers/i18n.vue";

export default {
  components: {lang},
  computed: {
    canPhone(){
        if(this.formReg.prefix_phone_1){
            return false
        }
        return true
    },
    yPhone(){
    return  this.formReg.prefix_phone_1+this.formReg.phone1;
    },
    passwordMatch() {
    return () => this.formReg.password === this.formReg.password_confirmation || "Password must match";
    }
  },
  methods: {
      save (date) {
          this.$refs.menu.save(date)
          this.formReg.dataBrith = date
      },
    validate() {


      if (this.$refs.registerForm.validate()) {
        // submit form to server/API here...
        //console.log(this.formReg);
        this.sendData(this.formReg);
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    sendData(data) {
      this.loading = true;
      this.axios
      .post("register", { data: { userData: data} })
      .then(response => {
          this.allerros = [];
          this.loading = false;
          if (response.data.errors) {
              //console.log(response.data.errors);
            response.data.errors.forEach(error => { this.openNotification('error', 'Error', error);});

          } else {

              //this.openNotification('success', 'Save', 'You have been store all data successfully');
              this.$router.push({ name: 'register/result' });
          }
      })
      .catch((error) => {
        this.loading = false;
        this.success = false;
        var errors =null;
        var status=error.response.status;
        //console.log(status);
            if (status == 422){
            errors=error.response.data.errors;
            //console.log(errors);
            errors.forEach(error => { this.openNotification('error', 'Error', error);});
          }else{
            this.openNotification('error','Error',error);
          }
      });
  },
  openNotification: function (type, m, d) {
      this.$notification.config({
          placement: 'topRight',
          top: 35,
          duration: 8,
      });
      this.$notification[type]({
        message: m,
        description: d,
      });
  },

  },
  data: () => ({
    activePicker: null,
    date: null,
    menu: false,
    platformName:"EatInMore",
    loading:false,
    sucess:false,
    formReg:{
      name:'',
      lastName:'',
      prefix_phone_1:'',
      phone1:'',
      dataBrith: '',
      email: '',
      password: '',
      password_confirmation:'',
    },
    userType:[
      {name:"Chef",type:"3"},
      {name:"Client",type:"2"},
    ],
    countrys:[],
    terms_conditions: false,
    dialog: true,
    tab: 1,
    tabs: [
        {name:"login", icon:"mdi-account"},
        {name:"register", icon:"mdi-account-outline"}
    ],
    valid: true,
    verify: "",
    loginPassword: "",
    loginEmail: "",
    loginEmailRules: [
      v => !!v || "Required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],
    emailRules: [
      v => !!v || "Required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],

    show1: false,
    rules: {
      required: value => !!value || "Required.",
      min: v => (v && v.length >= 8) || "Min 8 characters",
      password: value => {
          const pattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#*$%&])(?=.{8,})/;
          return (
            pattern.test(value) ||
            "Min. 8 characters with at least one capital letter, a number and a special character."
          );
        },

    },
    rulesPhone: {
      required: value => !!value || "Required.",
      min: v => (v && v.length >= 4) || "Min 4 digits",
      max: v => (v && v.length <= 15) || "Max 15 digits"
    }
  }),

  mounted() {
  this.axios
      .get('getCountryStates')
      .then(response => (this.countrys = response.data));
  },
  watch: {
    menu (val) {
      val && setTimeout(() => (this.activePicker = 'YEAR'))
    },
  },


};

</script>

